.DrawerStyle {
  display: none;
}

@media screen and (min-width: 769px) {
  .IconStyle {
    display: block;
    position: absolute;
    color: #fff;
    padding: 10px 0;
    left: 50px;
    bottom: 7px;
    -webkit-transition: .3s ease all;
    -o-transition: .3s ease all;
    transition: .3s ease all;
    -webkit-animation: showToggleButtonIconStyle .5s ease;
            animation: showToggleButtonIconStyle .5s ease; 
  }
  
  .DrawerStyle {
    color: #d33333;
    display: block;
    position: absolute;
    left: 230px;
    bottom: 7px;
    -webkit-transition: .3s ease all;
    -o-transition: .3s ease all;
    transition: .3s ease all;
    -webkit-animation: showToggleButtonDrawerStyle .5s ease;
            animation: showToggleButtonDrawerStyle .5s ease;
  }
}

@-webkit-keyframes showToggleButtonIconStyle {
  0% {
    opacity: 0;
    left: 30px;
    color: #9f311d;
  }
  25% {
    left: 60px;
  }
  50% {
    left: 40px;
  }
  75% {
    left: 55px;
  }
  100% {
    opacity: 100;
  }
}

@keyframes showToggleButtonIconStyle {
  0% {
    opacity: 0;
    left: 30px;
    color: #9f311d;
  }
  25% {
    left: 60px;
  }
  50% {
    left: 40px;
  }
  75% {
    left: 55px;
  }
  100% {
    opacity: 100;
  }
}

@-webkit-keyframes showToggleButtonDrawerStyle {
  0% {
    color: #d33333;
    left: 200px;
    opacity: 0;
  }
  25% {
    left: 240px;
  }
  50% {
    left: 220px;
  }
  75% {
    left: 235px;
  }
  100% {
    opacity: 100;
  }
}

@keyframes showToggleButtonDrawerStyle {
  0% {
    color: #d33333;
    left: 200px;
    opacity: 0;
  }
  25% {
    left: 240px;
  }
  50% {
    left: 220px;
  }
  75% {
    left: 235px;
  }
  100% {
    opacity: 100;
  }
}


